<template>
    <div class="trave_lookamap">
        <button v-on:click="goback()" v-if="isVisibilityDemo">返回上一级</button>
        <el-amap vid="amapDemo" :center="mapCenter" :zoom="12" class="amap-demo" v-if="isVisibilityDemo">
            <el-amap-marker v-for="(marker, index) in markers" :position="marker.position" :events="marker.events"  :vid="index" :key="index"></el-amap-marker>
        </el-amap>
        <div class="photoBox"  ref = "myElement" v-if="isVisibility">
            <children-item :content="photos"></children-item>
        </div>
        <button v-on:click="close()" v-if="isVisibility">关闭</button>
    </div>
</template>

<script>
import VueAMap from 'vue-amap'
import { AMapManager } from 'vue-amap';
import { constants } from 'fs';
import photo from '@/components/photo/photoView.vue'


let amapManager = new VueAMap.AMapManager();
var sum;
let positions;

export default {
    name:'lookamap',
    created(){
    },
    data: function() {
        return {
            isVisibility:false,
            isVisibilityDemo: true,
            photos:[],
            markers: [],
            searchOption: {
                city: '广州',
                citylimit: true
            },
            mapCenter: [114.746550, 22.673431]
        }
    },
    components: {
          "children-item": photo
     },
     mounted() {
        let markers = [];
        let self = this;
       
        self.$http.post('/api/traver')
            .then((res) => {
               positions = res.data;
               sum = positions.length;
               console.log(positions.length);
            },(err) => {
                console.log(err)
            });
        //let positions = [{"lng":102.704412,"lat":25.042165,"place":"云南"},{"lng":114.746550,"lat": 22.673431,"place":"巽寮湾"}];

        for (let i = 0 ; i < sum; i ++) {
          markers.push({
            position: [positions[i].lng, positions[i].lat],
            events: {
              click() {
                self.$nextTick(() => {
                  let param =  positions[i].place;
                  console.log("i am here:"+ positions.length);
                  console.log('this position: '+ positions[i].place);
                   self.$http.post('/api/photo')//,{param}
                    .then((res) => {
                           self.isVisibilityDemo = false;
                           self.isVisibility = true;
                           self.photos = res.data;
                           console.log("i am father : " + self.photos);
                        },(err) => {
                            console.log(err)
                        });
                });
                
              }
            }
          });

        }

        this.markers = markers;
    },
    methods:{
        goback: function () {
            this.$router.push({name:"HelloWorld"})
        },
        close : function () {
            console.log(this.isVisibility);
            this.isVisibility = false;
            this.isVisibilityDemo = true;
        }
       
    }
}
</script>

<style lang="less" scoped>

.amap-demo{
    margin: 0 auto;
    width: 90%;
    height: 500px;
}

</style>



